/********other mixins **********/
.html-wishlist-page td, .html-wishlist-page th {
  border-width: 0;
  padding: 20px;
}
.html-wishlist-page th {
  padding: 20px;
  white-space: nowrap;
  font-size: 1.125rem;
  color: #0f121b;
  font-weight: 300;
}
.html-wishlist-page td {
  min-width: 50px;
  background-color: #ffffff;
  padding: 10px;
  font-size: 1.125rem;
  color: #444441;
  font-weight: 400;
  border-bottom: none;
}
@media only screen and (min-width: 992px) {
  .html-wishlist-page td {
    border-bottom: 1px solid #eeeeee;
  }
}
.html-wishlist-page .wishlist-page .page-body {
  background-color: #F6F6F6;
  padding: 45px;
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .page-body {
    padding: 10px;
  }
}
@media only screen and (min-width: 992px) {
  .html-wishlist-page .wishlist-page .page-body {
    padding: 1px 45px 30px;
    min-height: 245px;
  }
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .page-body .wishlist-content {
    margin-bottom: 0px;
  }
}
