@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .table-wrapper {
    margin-bottom: 0px;
  }
}
.html-wishlist-page .wishlist-page .table-wrapper .cart {
  background-color: #ffffff;
}
@media only screen and (min-width: 768px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart {
    padding-bottom: 40px;
  }
}
@media only screen and (min-width: 992px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart {
    background-color: #F6F6F6;
    padding-bottom: 0px;
  }
}
.html-wishlist-page .wishlist-page .table-wrapper .cart thead tr th {
  background-color: #F6F6F6;
  font-size: 0.875rem;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart thead tr .remove-from-cart {
  border-bottom: none;
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tbody {
    padding: 10px;
  }
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr {
    margin: 20px 0 0;
  }
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr:first-child {
    margin-top: 0px;
  }
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr td {
  background-color: #ffffff;
  margin-bottom: 0px;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr td a {
  font-weight: 600;
  font-size: 0.875rem;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr td a:hover {
  color: #f0862e;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr td label, .html-wishlist-page .wishlist-page .table-wrapper .cart tr td span {
  font-size: 0.875rem;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr .unit-price {
  padding: 10px;
}
@media only screen and (min-width: 992px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr .product a {
    padding-left: 15px;
  }
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr .remove-from-cart {
  border-bottom: 0px;
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr .remove-from-cart {
    background-color: #ffffff;
  }
}
@media only screen and (max-width: 575px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr .remove-from-cart .remove-btn {
    background-size: 20px;
    width: 27px;
    height: 27px;
  }
}
@media only screen and (min-width: 992px) {
  .html-wishlist-page .wishlist-page .table-wrapper .cart tr .remove-from-cart {
    border-bottom: 1px solid #eeeeee;
  }
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr .product-picture {
  text-align: center;
}
.html-wishlist-page .wishlist-page .table-wrapper .cart tr .product-picture a img {
  height: 60px;
}
.html-wishlist-page .wishlist-page .table-wrapper .page-title h1 {
  margin: 10px;
  font-weight: 600;
}
.html-wishlist-page .wishlist-page .no-data {
  margin: 50px;
}